@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: "Inter", sans-serif;
    font-size: 16px;
}

.container.dark {
    background: rgb(30, 32, 42);
    background: linear-gradient(0deg, rgba(30, 32, 42, 1) 70%, rgba(31, 33, 46, 1) 70%);
}

.container.light {
    background: rgb(255, 255, 255);
    background: linear-gradient(0deg, rgba(255, 255, 255, 1) 70%, rgba(245, 247, 255, 1) 70%);
}

@media(max-width:800px){
    .container {
        height: 100%;
    }
}