.Followers {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style: none;
    width: 300px;
    padding: 30px 40px;
    border-radius: 5px;
}

.Followers:last-child {
    margin-right: 0;
}

.allFollowers.dark .Followers {
    background-color: hsl(228, 28%, 20%);
}

.allFollowers.light .Followers {
    background-color: hsl(227, 47%, 96%);
}

.Followers.facebook {
    border-top: 5px solid hsl(208, 92%, 53%);
}

.Followers.twitter {
    border-top: 5px solid hsl(203, 89%, 53%);
}

.Followers.instagram {
    border-top: 5px solid hsl(37, 97%, 70%);
}

.Followers.youtube {
    border-top: 5px solid hsl(348, 97%, 39%);
}

.Followers .user {
    display: flex;
    align-items: center;
    font-weight: 700;
    margin-bottom: 10px;
}

.allFollowers.dark .Followers .user {
    color: hsl(228, 34%, 66%);
}

.allFollowers.light .Followers .user {
    color: hsl(228, 12%, 44%);
}

.Followers .user i {
    font-size: 26px;
    margin-right: 10px;
}

.fa-facebook {
    color: hsl(208, 92%, 53%);
}

.fa-twitter {
    color: hsl(203, 89%, 53%);
}

.fa-instagram {
    color: hsl(37, 97%, 70%);
}

.fa-youtube {
    color: hsl(348, 97%, 39%);
}

.total {
    font-size: 52px;
    font-weight: 700;
}

.allFollowers.dark .Followers .total {
    color: hsl(0, 0%, 100%);
}

.allFollowers.light .Followers .total {
    color: hsl(230, 17%, 14%);
}

.text {
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-bottom: 30px;
}

.allFollowers.dark .Followers .text {
    color: hsl(228, 34%, 66%);
}

.allFollowers.light .Followers .text {
    color: hsl(228, 12%, 44%);
}

.today {
    font-weight: 700;
}

.up {
    color: hsl(163, 72%, 41%);
}

.down {
    color: hsl(356, 69%, 56%);
}

@media(max-width:1024px) {
    .Followers {
        width: 210px;
        padding: 20px;
    }

    .text {
        margin-bottom: 15px;
    }
}

@media(max-width:800px) {
    .Followers {
        margin-right: 0;
    }
}

@media(max-width:500px) {
    .Followers {
        width: 300px;
    }
}